<template>
    <ul id="personNal">
        <li>
            <span class="leftCon">真实姓名</span>
            <span class="rightCotent">{{name}}</span>
        </li>
        <li>
            <span class="leftCon lts4">性别</span>
            <span class="rightCotent">{{sex | sexFilter}}</span>
        </li>
        <li>
            <span class="leftCon">身份证号</span>
            <span class="rightCotent">{{idCard | idNumberFilter }}</span>
        </li>
        <li>
            <span class="leftCon">手机号码</span>
            <span class="rightCotent">{{phoneNumber | telFilter}}</span>
        </li>
        <li>
            <span class="leftCon">医保卡类型</span>
            <span class="rightCotent">{{hospitalCardType}}</span>
        </li>
        <li>
            <span class="leftCon">医保卡号</span>
            <span class="rightCotent">{{hospitalCard}}</span>
        </li>
        <li>
            <span class="leftCon">家庭住址</span>
            <span class="rightCotent">{{address}}</span>
        </li>
        <big-btn :content="content" :topNum="topNum" :rotateShow="rotateShow" @childClick="exit"></big-btn>
    </ul>
</template>

<script>
    import { apiUrl } from '@/common/js/api'
    import {ajaxPost} from '@/common/js/public'
    import jquery from '@/common/js/jquery'
    import { Toast , Notify } from 'vant'
    import bigBtn from './userCommon/bigBtn'
    export default {
        name: 'personNalMsg',
        data(){
            return {
                name: "",
                sex: "",
                idCard: "",
                phoneNumber: "",
                hospitalCardType: "",
                hospitalCard: "",
                address: "",
                content: "退出登录",
                topNum: "1.34rem",
                rotateShow: false
            }
        },
        components: {
            bigBtn
        },
        created(){
            
        },
        mounted(){
            this.getAllDetail();
        },
        methods:{
            childClick(){
                console.log(1);
            },
            notifyIt(msg, time = 1000) {
                this.$notify({
                    message: msg,
                    duration: time,
                })
            },
            exit() {
                var that = this;
                that.rotateShow = true;
                ajaxPost(apiUrl.logOut,{
                    "userId": JSON.parse(sessionStorage.getItem('userInfo')).userId
                }, function(res) {
                    if(res.data.body.resultCode == 1000){
                        that.rotateShow = false;
                        Toast({
                            type: 'success',
                            duration: 500,
                            message: '退出成功'
                        })
                        sessionStorage.removeItem('userInfo');
                        //sessionStorage.setItem('userInfo','')
                        //sessionStorage.setItem('userInfo','')
                        setTimeout(function(){
                            that.$router.push({name:"selectHospital"})
                        },500);
                    }else if(res.data.body.resultCode == 1110){
                        _this.notifyIt('业务异常');
                    }
                },function(error) {
                    console.log(error);
                    _this.notifyIt('网络繁忙');
                })
            },
            getAllDetail(){//获取个人信息
                var that = this;
                console.log(sessionStorage.getItem('userInfo'))
                ajaxPost(apiUrl.showPersonDetail,{
                    "userId": JSON.parse(sessionStorage.getItem('userInfo')).userId
                }, function(data) {
                    if(data.data.body.resultCode == 1000){
                        var data = data.data.body.data;
                        that.name = data.username;
                        that.sex = data.sex;
                        that.idCard = data.idcard;
                        that.phoneNumber = data.phone;
                        //that.hospitalCardType = data.hospitalType;
                        //that.hospitalCard = data.hospitalCard;
                        that.address = data.address;
                    }else if(data.data.body.resultCode == 2001 ){
                        console.log('用户不存在');
				        _this.notifyIt('用户不存在');
                    }else if(data.data.body.resultCode == 1001){
                        console.log('参数为空');
				        _this.notifyIt('参数为空');
                    }  
                }),function(error) {
                    console.log(error);
                    _this.notifyIt('网络繁忙');
                }
            }
        },
        filters:{
            idNumberFilter:function(myInput){
                var _leftNumber = myInput.toString().substring(0,6)
                var _rightNumber = myInput.toString().substring(14,myInput.length);
                var result = _leftNumber + '********' + _rightNumber;
                return result;
            },
            telFilter:function(myInput){
                var _leftNumber = myInput.toString().substring(0,3)
                var _rightNumber = myInput.toString().substring(7,myInput.length);
                var result = _leftNumber + '****' + _rightNumber;
                return result;
            },
            sexFilter:function(myInput){
                var result;
                if (myInput == 0){
                    result = '男'
                } else{
                    result = '女'
                }
                return result;
            },
        }
    }
</script>

<style scoped lang="less">
    #personNal{
        width: 100%;
        box-sizing: border-box;
        li{
            box-sizing: border-box;
            padding: 0.25rem 0.3rem;
            border-bottom: 1px solid #F6F7F8;
            font-size: 0.14*2rem;
            background-size: 0.04*2rem 0.1*2rem;
            color: #272727;
            .leftCon{
                line-height: normal;
            }
            .rightCotent{
                opacity: 0.6;
                float: right;
                line-height: normal;
            }
        }
        .exitButton{
            width: 3.2*2rem;
            height: 0.44*2rem;
            line-height: 0.44*2rem;
            text-align: center;
            background-color: #3391E8;
            color: #fff;
            margin: 0.67*2rem 0 0 0.27*2rem;
            border-radius: 4px;
            letter-spacing: 4px;
            font-size: 0.18*2rem;
            font-family: PingFangSC-Medium;
            box-shadow: 0px 4px 6px rgba(45,136,219,0.30);
        }
        .lts4{
            letter-spacing: 0.27*2rem;
        }
    }
</style>


